<template>
	<div>
		<div class="shopcart">
			<div class="content" @click="toggleList">
				<div class="content-left">
					<div class="logo-wrapper">
						<div class="logo" :class="{'highlight':totalCount>0}">
							<i class="icon-shopping_cart" :class="{'highlight':totalCount>0}"></i>
						</div>
						<div class="num" v-show="totalCount>0">{{totalCount}}</div>
					</div>
					<div class="price" :class="{'highlight':totalPrice>0}">￥{{totalPrice}}</div>
					<div class="desc">另需配送费￥{{deliveryPrice}}元</div>
				</div>
				<div class="content-right" @click.stop.prevent="pay">
					<div class="pay" :class="payClass">
						{{payDesc}}
					</div>
				</div>
				<div class="ball-container">
					<div v-for="ball in balls">
						<transition name="drop" @before-enter="beforeDrop" @enter="dropping" @after-enter="afterDrop">
							<div v-show="ball.show" class="ball">
								<div class="inner inner-hook"></div>
							</div>
						</transition>
					</div>
					<!-- <transition-group name="drop" @before-enter="beforeDrop" @enter="dropping" @after-enter="afterDrop">
		        <div class="ball" v-for="(ball,index) in balls" :key="index" v-show="ball.show">
		          <span class="inner inner-hook icon-remove_circle_outline"></span>
		        </div>
		      </transition-group> -->
				</div>
				<transition name="fold">
					<div class="shopcart-list" v-show="listShow">
						<div class="list-header">
							<h1 class="title">购物车</h1>
							<span class="empty" @click="empty">清空</span>
						</div>
						<div class="list-content" ref="listContent">
							<ul>
								<li class="food" v-for="food in selcetFoods">
									<span class="name">{{food.name}}</span>
									<div class="price">
										<span class="">￥{{food.price*food.count}}</span>
									</div>
									<div class="cartcontrol-wrapper">
										<cartcontrol :food="food"></cartcontrol>
									</div>
								</li>
							</ul>
						</div>
					</div>
				</transition>
				
			</div>
		</div>
		<transition name="fade">
			<div class="list-mask" @click="hideList" v-show="listShow"></div>
		</transition>
		
	</div>
</template>
<script>
	import BScroll from 'better-scroll';
	import cartcontrol from '../cartcontrol/cartcontrol.vue'

	export default {
		props:{
			selcetFoods:{
				type: Array,
				default(){
					return [
						{
							price: 8,
							count: 3
						}
					];
				}
			},
			deliveryPrice:{
				type: Number,
				default: 0	//没有传值时的默认值
			},
			minPrice:{
				type: Number,
				default: 0
			}
		},
		data(){
			return {
				balls: [
					{
						show: false
					},
					{
						show: false
					},
					{
						show: false
					},
					{
						show: false
					},
					{
						show: false
					}
				],
				dropBalls:[],
				fold: true
			}
		},
		computed: {
			totalPrice() {
				let total = 0;
				this.selcetFoods.forEach((food) => {
					total += food.price * food.count;
				});
				return total;
			},
			totalCount() {
				let count = 0;
				this.selcetFoods.forEach((food) => {
					count += food.count;
				});
				return count;
			},
			payDesc() {
				if(this.totalPrice === 0){
					return `￥${this.minPrice}元起送`;
				}else if(this.totalPrice<this.minPrice){
					let diff = this.minPrice - this.totalPrice;
					return `还差￥${diff}元起送`;
				}else{
					return '去结算';
				}
			},
			payClass(){
				if(this.totalPrice<this.minPrice){
					return 'not-enough';
				}else{
					return 'enough'
				}
			},
			listShow(){
				if(!this.totalCount){
					this.fold = true;
					return false;
				}
				let show = !this.fold;
				if(show){
					this.$nextTick(()=>{
						if(!this.scroll){
							this.scroll = new BScroll(this.$refs.listContent,{
								click: true
							})
						}else{
							this.scroll.refresh();
						}
						
					})
				}
				return show;
			}
		},
		methods:{
			//这个drop方法在goods组件中被调用（通过$refs.shopcart.drop
			drop(el){
				console.log("shopcart drop");
        for (let i = 0; i < this.balls.length; i++) {
          let ball = this.balls[i];
          //在数组中找到当前状态下第一个show为false的球完成这次点击动画。（假设balls数组中设置了5个对象：把动画时间设置长一点，连续点击+号按钮就可以看到，最多同时出现5个“小球”动画）
          if (!ball.show) {
            ball.show = true;
            ball.el = el;	 //保存传入的DOM
            this.dropBalls.push(ball);	//存入已经在下落的小球
            return;
          }
        }
			},
			beforeDrop(el){
				let count = this.balls.length;		//遍历所有小球
				while (count--) {
					let ball = this.balls[count];
					if (ball.show) {		//如果是在下落的小球
						let rect = ball.el.getBoundingClientRect();	//浏览器接口：获得元素相当于视口的位置。（ball的el属性是在drop方法里设置的）
						//在css的.ball{}里设置小球的最终位置。这里设置的x，y是小球的起始位置（相对于最终位置，所以y是负值）
						let x = rect.left - 32;
						let y = -(window.innerHeight - rect.top - 22);
						console.log(`rect.left:${rect.left},rect.top:${rect.top}`)
						console.log(`x:${x},y:${y}`)
						// debugger
						el.style.display = '';	//置空，即显示出来
						//外层元素做纵向动画
						el.style.webkitTransform = `translate3d(0,${y}px,0)`;
						el.style.transform = `translate3d(0,${y}px,0)`;
						//内层元素做横向动画
						let inner = el.getElementsByClassName('inner-hook')[0];
						inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
						inner.style.transform = `translate3d(${x}px,0,0)`;
					}
				}
			},
			dropping(el, done) {
				/* eslint-disable no-unused-vars */
				let rf = el.offsetHeight;	//这一句用于主动触发浏览器重绘（测试发现不设置的话，在点击加号时小球下落和减号出现这两个动画可能会有一个失效
				this.$nextTick(() => {
					el.style.webkitTransform = 'translate3d(0,0,0)';
					el.style.transform = 'translate3d(0,0,0)';
					let inner = el.getElementsByClassName('inner-hook')[0];
					inner.style.webkitTransform = 'translate3d(0,0,0)';
					inner.style.transform = 'translate3d(0,0,0)';
					el.addEventListener('transitionend', done);	//加了这行，<tansition>才能监听到after-enter
				});
			},
			afterDrop(el) {
				let ball = this.dropBalls.shift();
				if (ball) {
					ball.show = false;
					el.style.display = 'none';
				}
			},
			toggleList(){
				if(!this.totalCount){
					return;
				}
				this.fold = !this.fold;
			},
			empty(){
				this.selcetFoods.forEach((food)=>{
					food.count = 0;
				})
			},
			hideList(){
				this.fold = true;
			},
			pay(){
				if(this.totalPrice<this.minPrice){
					return;
				}
				window.alert(`支付${this.totalPrice}元`)
			}
		},
		components:{
			cartcontrol
		}
	};
</script>
<style lang="stylus">
	@import "../../common/stylus/mixin.styl"

	.shopcart
		position: fixed
		left: 0
		bottom: 0
		z-index: 50
		width: 100%
		height: 48px
		.content
			display: flex
			background: #141d27
			font-size: 0
			color: rgba(255, 255, 255, 0.4)
			.content-left
				flex: 1
				.logo-wrapper
					display: inline-block
					vertical-align: top
					position: relative
					top: -10px
					margin: 0 12px
					padding: 6px
					width: 56px
					height: 56px
					box-sizing: border-box
					border-radius: 50%
					background: #141d27
					.logo
						width: 100%
						height: 100%
						border-radius: 50%
						text-align: center
						background: #2b343c
						&.highlight
							background: rgb(0, 160, 220)
						.icon-shopping_cart
							line-height: 44px
							font-size: 24px
							color: #80858a
							&.highlight
								color: #fff
					.num
						position: absolute
						top: 0
						right: 0
						width: 24px
						height: 16px
						line-height: 16px
						text-align: center
						border-radius: 16px
						font-size: 9px
						font-weight: 700
						color: #fff
						background: rgb(240, 20, 20)
						box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4)
				.price
					display: inline-block
					vertical-align: top
					margin-top: 12px
					line-height: 24px
					padding-right: 12px
					box-sizing: border-box
					border-right: 1px solid rgba(255, 255, 255, 0.1)
					font-size: 16px
					font-weight: 700
					&.highlight
						color: #fff
				.desc
					display: inline-block
					vertical-align: top
					margin: 12px 0 0 12px
					line-height: 24px
					font-size: 10px
			.content-right
				flex: 0 0 105px
				width: 105px
				.pay
					height: 48px
					line-height: 48px
					text-align: center
					font-size: 12px
					font-weight: 700
					&.not-enough
						background: #2b333b
					&.enough
						background: #00b43c
						color: #fff
		.ball-container
			.ball
				position: fixed
				left: 32px
				bottom: 22px
				z-index: 200
				color:#fff
				// &.drop-transition
				&.drop-enter,&.drop-enter-active
					transition: all 0.4s cubic-bezier(.99,-0.46,.39,1.07)
					.inner
						display:inline-block
						width: 16px
						height: 16px
						border-radius: 50%
						background: rgb(0, 160, 220)
						transition: all 0.4s linear		//注意：x轴运动是线性的
		.shopcart-list
			position: absolute
			left: 0
			top: 0
			z-index: -1
			width: 100%
			transform: translate3d(0, -100%, 0)	//-100%是往上移动等于自身高度的距离
			&.fold-enter-active,&.fold-leave-active
				transition: all 0.5s
				// transform: translate3d(0, -100%, 0)
			&.fold-enter, &.fold-leave-to
				transform: translate3d(0, 0, 0)
			.list-header
				height: 40px
				line-height: 40px
				padding: 0 18px
				background: #f3f5f7
				border-bottom: 1px solid rgba(7, 17, 27, 0.1)
				.title
					float: left
					font-size: 14px
					color: rgb(7, 17, 27)
				.empty
					float: right
					font-size: 12px
					color: rgb(0, 160, 220)

			.list-content
				padding: 0 18px
				max-height: 217px
				overflow: hidden
				background: #fff
				.food
					position: relative
					padding: 12px 0
					box-sizing: border-box
					border-1px(rgba(7, 17, 27, 0.1))
					.name
						line-height: 24px
						font-size: 14px
						color: rgb(7, 17, 27)
					.price
						position: absolute
						right: 90px
						bottom: 12px
						line-height: 24px
						font-size: 14px
						font-weight: 700
						color: rgb(240, 20, 20)
					.cartcontrol-wrapper
						position: absolute
						right: 0
						bottom: 6px

	.list-mask
		position: fixed
		top: 0
		left: 0
		width: 100%
		height: 100%
		z-index: 40
		filter: blur(10px)
		opacity: 1
		background: rgba(7, 17, 27, 0.6)
		&.fade-enter-active,&.fade-leave-active
			transition: all 0.5s
		&.fade-enter, &.fade-leave-active
			opacity: 0
			background: rgba(7, 17, 27, 0)
</style>


