<template>
  <div class="goods">
  	<div class="menu-wrapper" ref="menuWrapper">
  		<ul>
  			<li v-for="(item,index) in goods" class="menu-item" :class="{'current':currentIndex===index}" @click="selectMenu(index)">
  				<span class="text border-1px">
  					<span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
  				</span>
  			</li>
  		</ul>
  	</div>
  	<div class="foods-wrapper" ref="foodsWrapper">
  		<ul>
  			<li v-for="item in goods" class="food-list foot-list-hook">
  				<h1 class="title">{{item.name}}</h1>
  				<ul>
  					<li @click="selectFood(food,$event)" v-for="food in item.foods" class="food-item border-1px">
  						<div class="icon">
  							<img :src="food.icon">
  						</div>
  						<div class="content">
  							<h2 class="name">{{food.name}}</h2>
  							<p class="desc">{{food.description}}</p>
  							<div class="extra">
  								<span class="count">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
  							</div>
  							<div class="price">
  								<span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
  							</div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food="food" @cartAdd="addFood"></cartcontrol>
                </div>
  						</div>
  					</li>
  				</ul>
  			</li>
  		</ul>
  	</div>
    <shopcart ref="shopcart" :selcetFoods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>
    <food :food="selectedFood" ref="food" @cartAdd="addFood"></food>
  </div>
</template>

<script>
  import BScroll from 'better-scroll';
  import shopcart from '../shopcart/shopcart.vue'
  import cartcontrol from '../cartcontrol/cartcontrol.vue'
  import food from '../food/food.vue'

	const ERR_OK = 0;

  export default{
    props: {
    	seller:{
    		type: Object
    	}
    },
    data(){
    	return {
    		goods: [],
        listHeight: [],  //每个商品区块的位置（绝对高度）
        scrollY: 0,
        selectedFood: {},
    	};
    },
    computed:{
      currentIndex(){
        for(let i=0; i<this.listHeight.length; i++){
          let height1 = this.listHeight[i];
          let height2 = this.listHeight[i+1];
          if(!height2 || (this.scrollY >= height1 && this.scrollY < height2)){  //!height2 === true 说明到最后一个区间了
            //滚动坐标落在某个区间时
            return i;
          }
        }
        return 0;
      },
      selectFoods() {
        let foods = [];
        this.goods.forEach((good) => {
          good.foods.forEach((food) => {
            if(food.count){ //有food被选中

              foods.push(food);
            }
          });
        });
        return foods;
      }
    },
    created(){
    	this.classMap = ['decrease','discount','special','invoice','guarantee'];
    	this.$http.get('/api/goods').then((response) => {
    		response = response.body;
    		if(response.errno === ERR_OK){
    			this.goods = response.data;
    			// console.log(this.goods);
    			this.$nextTick(() => {
            this._initScroll();
            this._calculateHeight();
          });
    		}
    	})
    },
    methods: {
      selectMenu(index){
        if(!event._constructed){
          return;
        }
        let foodList = this.$refs.foodsWrapper.getElementsByClassName('foot-list-hook');
        let el = foodList[index];
        this.foodsScroll.scrollToElement(el, 300);  //要滚动到的element，动画的时间
        console.log(index);
      },
      selectFood(food,event){
        if(!event._constructed){
          return;
        }
        this.selectedFood = food;
        this.$refs.food.show();
      },
      addFood(target){
        this._drop(target);
      },
      _drop(target){
        //体验优化，异步执行小球下落动画
        this.$nextTick(()=>{  //点击加号时，避免同时开始两个动画效果导致卡顿
          this.$refs.shopcart.drop(target); //
        })
      },
    	_initScroll() {
    		this.meunScroll = new BScroll(this.$refs.menuWrapper, {
          click: true //初始化这个属性，menuScroll才可以触发自定义的click事件
        });
    		this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
          click: true,
          probeType: 3  //作用见better scroll文档
        });

        this.foodsScroll.on('scroll', (pos) => {
          this.scrollY = Math.abs(Math.round(pos.y)); //实时监测foodsScroll的滚动高度
        })
    	},
      _calculateHeight(){
        let foodList = this.$refs.foodsWrapper.getElementsByClassName('foot-list-hook');
        let height = 0; //第一个区块的起始高度为0
        this.listHeight.push(height);
        for(let i=0; i<foodList.length; i++){
          let item = foodList[i];
          height += item.clientHeight;  //累加每个区块的高度
          this.listHeight.push(height);
        }
        console.log(this.listHeight);
      }
    },
    components:{
      shopcart,
      cartcontrol,
      food
    }
    
  }
</script>

<style lang="scss">

	@mixin bg-image($url){
		background-image: url($url + "@2x.png");	
		// @media写在内部行不行？没测试过
		@media (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3){
			background-image: url($url + "@3x.png");
		}
	}

  .goods{
    display: flex;
    position: absolute;
    top: 174px;
    bottom: 46px;
    width: 100%;
    overflow: hidden;
    .menu-wrapper{
      flex: 0 0 80px;
      width: 80px;
      background: #f3f5f7;
      .menu-item{
        display: table;
        height: 54px;
        width: 56px;
        padding: 0 12px;
        line-height: 14px;
        &.current{
          position: relative;
          z-index: 10;
          margin-top: -1px;
          background: #fff;
          font-weight: 700;
          .text{
            // border-none();
          }
        }
        .icon{
          display: inline-block;
          vertical-align: top;
          width: 12px;
          height: 12px;
          margin-right: 2px;
          background-size: 12px 12px;
          background-repeat: no-repeat;
          &.decrease{
            @include bg-image('decrease_3');
          }
          &.discount{
            @include bg-image('discount_3');
          }
          &.guarantee{
            @include bg-image('guarantee_3');
          }
          &.invoice{
            @include bg-image('invoice_3');
          }
          &.special{
            @include bg-image('special_3');
          }
        }
        .text{
          display: table-cell;
          width: 56px;
          vertical-align: middle;
          // border-1px(rgba(7, 17, 27, 0.1));
          font-size: 12px;
        }
      }
    }
    .foods-wrapper{
      flex: 1;
      .title{
        padding-left: 14px;
        height: 26px;
        line-height: 26px;
        border-left: 2px solid #d9dde1;
        font-size: 12px;
        color: rgb(147, 153, 159);
        background: #f3f5f7;
      }
      .food-item{
        display: flex;
        margin: 18px;
        padding-bottom: 18px;
        // border-1px(rgba(7, 17, 27, 0.1));
        &:last-child{
          // border-none();
          margin-bottom: 0;
        }
        .icon{
          flex: 0 0 57px;
          margin-right: 10px;
        }
        .content{
          flex: 1;
          .name{
            margin: 2px 0 8px 0;
            height: 14px;
            line-height: 14px;
            font-size: 14px;
            color: rgb(7, 17, 27);
          }
          .desc, .extra{
            line-height: 10px;
            font-size: 10px;
            color: rgb(147, 153, 159);
          }
          .desc{
            line-height: 12px;
            margin-bottom: 8px;
          }
          .extra{
            .count{
              margin-right: 12px;
            }
          }
          .price{
            font-weight: 700;
            line-height: 24px;
            .now{
              margin-right: 8px;
              font-size: 14px;
              color: rgb(240, 20, 20);
            }
            .old{
              text-decoration: line-through;
              font-size: 10px;
              color: rgb(147, 153, 159);
            }
          }
          .cartcontrol-wrapper {
            position: absolute;
            right: 0;
            bottom: 12px;
          }
        }
      }
    }
  }
</style>



