<template>
  <div id="app">
    <v-header :seller="seller"></v-header>
    <div class="tab border-1px">
      <div class="tab-item">
        <router-link :to="{path:'/goods'}">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link :to="{path:'/ratings'}">评论</router-link>
      </div>
      <div class="tab-item">
        <router-link :to="{path:'/seller'}">商家</router-link>
      </div>
    </div>
    <keep-alive>
      <router-view :seller="seller"></router-view>
    </keep-alive>
  </div>
</template>

<script>
  import {urlParse} from './common/js/util.js';
  import header from './components/header/header.vue';

  const ERR_OK = 0; //状态码

  export default{
    data() {
      return {
        seller: {
          //一个立即执行的方法
          id: (()=>{
            let queryParam = urlParse();
            console.log(queryParam)
            return queryParam.id;
          })()
        }
      }
    },
    created() {
      this.$http.get('/api/seller').then((response) => {
      // this.$http.get('/api/seller?id='+this.seller.id).then((response) => {
        /*拿到返回的json对象。 这是vue-resource的方法，
        可以查文档 https://github.com/pagekit/vue-resource/blob/develop/docs/http.md
        在response中
        */
        var res = response.body;
        if( res.errno === ERR_OK ){
          // this.seller = res.data;
          this.seller = Object.assign({},this.seller,res.data);  //vue建议的扩展属性的方法
          // console.log(this.seller.id)
        }
        
      });
    },
    components: {
      'v-header': header
    }
  }
</script>

<style lang="scss">

  #app {
    .tab {
      display: flex;
      width: 100%;
      height: 40px;
      line-height: 40px;
      .tab-item {
        flex: 1;
        text-align: center;
        a {   //router-link将被渲染成a标签
          display: block;
          font-size: 14px;
          color: rgb(77, 85, 93);
          &.v-active{
            color: rgb(240, 20, 20);
          }
        }
      }
    }
  }

</style>



