/**
** 一个通用的localstorage存取的js库
**/

export function saveToLocal(id, key, value){
	let seller = window.localStorage.__seller__;
	//第一次不存在seller时（不存在 localStorage.__seller__），则进行赋值.存在则检查是否存在对应的id
	if(!seller){
		seller = {};
		seller[id] = {};
	}else{
		seller = JSON.parse(seller);	//从localStorage里拿到的是json，需要解析
		if(!seller[id]){
			seller[id] = {};
		}
	}
	seller[id][key] = value;
	window.localStorage.__seller__ = JSON.stringify(seller);	//localStorage只能存储json
};

export function loadFromLocal(id, key, def){
	let seller = window.localStorage.__seller__;
	if(!seller){
		return def;	//def：读取不到时返回的默认值
	}
	seller = JSON.parse(seller)[id];
	if(!seller){
		return def;
	}
	let ret = seller[key];
	return ret || def;
};


